<template>
  <transition name="slide-right">
    <div
      v-if="shown"
      class="container">
      <div class="banner">
        <a class="beta">BETA</a>
        <div class="message">
          <slot />
        </div>
        <a
          class="feedback"
          href="mailto:feedback@ucommerce.net"
          target="_blank">Feedback</a>
        <button
          class="close"
          @click="hide">
          
        </button>
      </div>
    </div>
  </transition>
</template>

<script>
import { localStore } from '@/utils/storage.js'
export default {
  name: 'BetaBanner',
  data () {
    const hidden = localStore.get('hideBetaBanner')
    return {
      shown: !hidden,
    }
  },
  methods: {
    hide () {
      localStore.set('hideBetaBanner', true)
      this.shown = false
    },
  },
}
</script>

<style lang="stylus" scoped>
.container
  position fixed
  right 0
  bottom 0
  left 0
  z-index 200
  margin $base-unit * 2

  .banner
    display grid
    grid-template-columns 120px auto 120px 80px
    justify-items start
    width 100%
    height 80px
    border-radius $base-border-radius
    color #fff
    font-size 12px
    background-color $base-color-brand
    place-items center

  .beta
    justify-self center
    font-weight bold
    font-size 20px

  .feedback
    display flex
    align-items center
    justify-content center
    width 101px
    height 34px
    border solid 1px rgba(255, 255, 255, 0.3)
    border-radius 17px
    color #fff
    font-weight 600
    font-size 14px
    text-decoration none
    cursor pointer

  .close
    width 24px
    height 24px
    border solid 1px rgba(255, 255, 255, 0.3)
    border-radius: 20px
    color #fff
    font-size 14px
    background-color transparent
    cursor pointer

.slide-right-leave-active
.slide-right-active
  transition all 0.5s ease-in

.slide-right-enter
  transform translate(-500px, 0)
  opacity 1

.slide-right-leave-to
  transform translate(500px, 0)
  opacity 0
</style>
