<template>
  <section class="advanced-metrics">
    <div class="advanced-metrics__intervals">
      <button
        ref="dayBtn"
        class="interval-toggle active"
        @click="changeInterval($event, 1)">
        {{ $t('metrics.day') }}
      </button>
      <button
        ref="monthBtn"
        class="interval-toggle"
        @click="changeInterval($event, 30)">
        {{ $t('metrics.month') }}
      </button>
      <button
        ref="yearBtn"
        class="interval-toggle"
        @click="changeInterval($event, 365)">
        {{ $t('metrics.year') }}
      </button>
    </div>
    <div
      v-for="(storeStatistics, index) in metrics"
      :key="index"
      class="advanced-metrics__section">
      <div class="advanced-metrics__store-header">
        <div class="store__name">
          {{ storeStatistics.name }}
        </div>
        <span class="advanced-metrics__interval">{{ timeInterval }}</span>
      </div>

      <div
        v-if="!storeStatistics.revenues.length"
        class="revenue-row">
        <span class="revenue-value">-</span>
        <span class="revenue__order">0 {{ $tc('metrics.order', 0) }}</span>
      </div>
      <div
        v-for="revenue in storeStatistics.revenues"
        :key="revenue.ISOCode"
        class="revenue-row">
        <div>
          <span class="revenue-value">{{ `${formatRevenue(revenue.revenueLastPeriod, revenue.amount)} ${getSymbol(revenue.ISOCode)}` }} </span>
          <span
            :class="{'positive': revenue.differenceInPercentageLastPeriod >= 0}"
            class="revenue__difference">{{ revenue.differenceInPercentageLastPeriod }}% </span>
        </div>
        <div>
          <span class="revenue__order">{{ revenue.ordersCount }} {{ $tc('metrics.order', revenue.ordersCount) }}</span>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import symbolMixin from '../js/symbol-mixin'
import { Format, locales } from '@/utils/date-formatter'
const ACTIVE = 'active'

export default {
  name: 'MetricsAdvanced',
  mixins: [symbolMixin],
  computed: {
    metrics () {
      return this.$store.getters.metrics.storeRevenueStatistics
    },
    timeInterval () {
      const days = this.$store.getters.daysDisplayed
      const locale = locales[this.$store.getters.locale]
      const format = days > 300 ? 'MMM DD, YYYY' : 'MMM DD'
      const date = new Date()
      const rightDate = Format(date, format, locale)
      const leftDate = days > 1 ? `${Format(date.setDate(date.getDate() - days), format, locale)} -` : ''
      return `${leftDate} ${rightDate}`
    },
  },
  methods: {
    changeInterval (event, days) {
      this.$refs.dayBtn.classList.remove(ACTIVE)
      this.$refs.monthBtn.classList.remove(ACTIVE)
      this.$refs.yearBtn.classList.remove(ACTIVE)
      event.target.classList.add(ACTIVE)
      this.$store.dispatch('setDaysShown', days)
      this.$emit('daysChanged')
    },
    formatRevenue (lastPeriod, amount) {
      const fixedSum = +(lastPeriod + amount).toFixed(0)
      return fixedSum.toLocaleString()
    },
  },
}
</script>

<style lang="stylus" scoped>
.advanced-metrics
  min-height 150px
  margin-top 15px
  border-radius 2px
  background-color #fbfbfb
  box-shadow 1px 1px 5px rgba(0,0,0,0.3)

.advanced-metrics__intervals
  padding 10px 0 0 20px

.interval-toggle
  margin-right 8px
  padding 6px 15px 8px
  border none
  border-radius 3px
  color $legacy-color-dark-gray-200
  font-weight 600
  font-size 0.875rem
  background-color #f1efef
  outline none
  cursor pointer

  &.active
    background-color #dcdcdc
    cursor default
    pointer-events none

.advanced-metrics__section
  padding 10px 20px 20px
  border-bottom 1px solid rgba(0,0,0,0.1)

  &:last-child
    border-bottom none

.advanced-metrics__store-header
  display flex
  justify-content space-between
  margin-top 12px
  margin-bottom 12px
  color $legacy-color-dark-gray-100
  font-size 0.8125rem

.revenue-row
  display flex
  align-items baseline
  justify-content space-between
  margin-bottom 6px

.revenue-value
  font-weight 600
  font-size 1.5rem

.revenue__difference
  display inline-block
  color $base-color-red
  font-weight 600
  font-size 0.625rem

  &::before
    margin-right 3px
    margin-left 8px
    font-size 0.5rem
    content ''

  &.positive
    color $base-color-green

    &::before
      content ''

.revenue__order
  font-size 0.875rem

</style>
